/*Universal style*/
html {
	position: relative;
	height: 100%;
	overflow: 'hidden';
}
body {
	font-family: Arial, Helvetica, sans-serif;
	height: 100%;
	overflow: hidden;
	position: relative;
	margin: 0%;
}

#root {
	height: 100%;
	position: relative;
}

.electionSelect {
	min-width: 150px !important;
	padding: 10px

}

.leaflet-container {
	height: 100%;
	width: 100%;
  z-index: 1;
  overflow: hidden;
  touch-action:none;
  -ms-touch-action: none;
  background: #fff !important;
}

.leaflet-control-zoom {
	top: 20px;
}

#zoomOut {
	color: black;
  }


.recharts-wrapper {
	display: flex;
	justify-content: center;
	/* height: 50% !important;
	width: 50% !important; */
}

.tooltip-wrapper {
	border: 1px solid #f5f5f5;
	border-radius: 3px;
	background-color: rgba(243, 243, 243, 0.959);
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
} .tooltip-name {
	padding-bottom: 10px;
}

:root {
	--bg:  #e0e0e0;;
	--bg-accent: #4d4d4d;
	--text-color: #dadce1;
	--nav-size: 60px;
	--border: 1px solid #474a4d;
	--border-radius: 8px;
	--speed: 600ms; 
	--boxShadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
  }

  .recharts-tooltip-wrapper {
	  box-shadow: 0px 0px 7px 0px black;
  }

.scrollingDiv {
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
	position: relative;
	height: 100%;
	scroll-behavior: smooth;
}

.menu {
	/* position: absolute; */
	width: 100%;
	display: flex;
	flex-direction: column;
	height: 100%;
	position: relative;
	overflow: hidden;
	/* margin-top: 80px; */
  }
  /* .menu button:hover {
	background-color: #131e29;
  } */

  .menu #provinceButton {
	  position: relative;
	  padding: 0px 16px;
	  display: flex;
	  align-items: center;
	  min-height: 48px;
	  cursor: pointer;
  }

  .menu #provinceButton + #provinceButton {
    border-top: 1px solid #dcdcdc;
}

  
  .enter-right {
	position: absolute;
	transform: translateX(-110%);
  }
  .enter-right-active {
	transform: translateX(0%);
	transition: all var(--speed) ease;
  }
  .exit-left {
	position: absolute;
  }
  .exit-left-active {
	transform: translateX(-110%);
	transition: all var(--speed) ease;
  }


  .enter-left {
	position: 'absolute';
	transform: translateX(110%);
  }
  .enter-left-active {
	transform: translateX(0%);
	transition: all var(--speed) ease;
  }
  .exit-right {
  position: absolute;
  }
  .exit-right-active {
	transform: translateX(110%);
	transition: all var(--speed) ease;
  }

  .lds-ring {
	display: inline-block;
	position: relative;
	width: 80px;
	height: 80px;
  }
  .lds-ring div {
	box-sizing: border-box;
	display: block;
	position: absolute;
	width: 64px;
	height: 64px;
	margin: 8px;
	border: 8px solid rgb(187, 187, 187);
	border-radius: 50%;
	animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	border-color: rgb(187, 187, 187) transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
	animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
	animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
	animation-delay: -0.15s;
  }
  @keyframes lds-ring {
	0% {
	  transform: rotate(0deg);
	}
	100% {
	  transform: rotate(360deg);
	}
  }

#filteredCandidate + #filteredCandidate {
	border-top: 1px solid black;
}

::-webkit-scrollbar {
	width: 5px;
	height: 5px;
}

::-webkit-scrollbar-track {
	border-radius: 10px
}

::-webkit-scrollbar-thumb {
	background: rgb(192, 192, 192);
}

::-webkit-scrollbar-thumb:hover {
	background: rgb(104, 104, 104);; 
  }

.MuiAccordionSummary-root.Mui-expanded {
	margin: 10px 0px !important;
	height: 40px !important;
	min-height: 0px !important;
}

.MuiDialogTitle-root {
	padding: 10px !important;
}

#combo-box-demo-label {
	padding-right: 15px;
}

@media (orientation: portrait) {
	#mapAppContainer {
		flex-direction: column !important;
	}
	#sideBarDiv {
		flex: 6 !important;
	}
	#sidebarTitle {
		font-size: 20px !important;
		
	}
}

@media only screen and (max-width: 1300px) {
	#sidebarTitle div {
		font-size: 18px !important;
	}
}


@media only screen and (max-width: 600px) {
	#partyCard, #widgetTitleRow {
		flex-direction: column;
	}
	#partyCard #popVote {
		font-size: 14px
	}
	#widgetTitle {
		font-size: 20px;
	}
	#declarationText span {
		font-size: 14px;
	}
	#majority {
		font-size: 12px;
	}
	.animate-bar {
		height: 8px !important;
	}
	#tabDiv {
		visibility: visible;
	}
	.leaflet-tooltip-pane {
		/* visibility: hidden; */
		z-index: 1200;
	}
	#resultsTooltip {
		width: 150px;
	}
	#ridingNameTooltip, #tooltipWinnerName {
		font-size: 16px !important;
	}
	#tooltipPartycode {
		font-size: 12px !important;
	}
	.MuiDialog-paper {
		margin: 10px !important;
	}
}

@media only screen and (max-width: 500px) {
	#tab {
		font-size: 10px;
	}
}